"use client";

import TabsButton from "@/src/components/ui/TabsButton";
import { SIDEBAR_WIDTH } from "@/src/constants/constants";
import { Quotation } from "@/src/types/quotations/quote.types";
import axios from "axios";
import Link from "next/link";
import React, { JSX, useState, useEffect, useRef } from "react";
import { createPortal } from "react-dom";
import { IoIosSearch } from "react-icons/io";
import { MdOutlineEdit, MdOutlineLeaderboard } from "react-icons/md";
import extractErrorMessages from "../../utils/error.utils";
import { toast } from "react-toastify";
import Skeleton from "react-loading-skeleton";
import { FaRegEye, FaChevronDown, FaCheck } from "react-icons/fa";
import { usePermissions } from "@/src/hooks/usePermissions";
import { PERMISSIONS, QuotationStatus } from "@/src/constants/enum";
import { QuoteService } from "@/src/services/quote.service";
import { formatDate } from "../../utils/date.utils";
import { handleCopy } from "../../utils/clipboard.utils";

const STATUS_CONFIG = {
  [QuotationStatus.DRAFT]: { label: "Draft", color: "bg-gray-600" },
  [QuotationStatus.SENT]: { label: "Sent", color: "bg-blue-600" },
  [QuotationStatus.UPDATED]: { label: "Updated", color: "bg-amber-500" },
  [QuotationStatus.RESEND]: { label: "Resend", color: "bg-indigo-600" },
  [QuotationStatus.VIEWED]: { label: "Viewed", color: "bg-purple-600" },
  [QuotationStatus.ACCEPTED]: { label: "Accepted", color: "bg-green-600" },
  [QuotationStatus.REJECTED]: { label: "Rejected", color: "bg-red-600" },
  [QuotationStatus.EXPIRED]: { label: "Expired", color: "bg-orange-600" },
  [QuotationStatus.DELETE]: { label: "Delete", color: "bg-red-800" },
};

const AVAILABLE_STATUSES = [
  QuotationStatus.DRAFT,
  QuotationStatus.SENT,
  QuotationStatus.UPDATED,
  QuotationStatus.RESEND,
  QuotationStatus.VIEWED,
  QuotationStatus.ACCEPTED,
  QuotationStatus.REJECTED,
  QuotationStatus.EXPIRED,
];

const StatusDropdown = ({
  currentStatus,
  quoteId,
  onStatusUpdate,
}: {
  currentStatus: QuotationStatus;
  quoteId: string;
  onStatusUpdate: (id: string, newStatus: QuotationStatus) => Promise<void>;
}) => {
  const [err, setErr] = useState("");
  const [isOpen, setIsOpen] = useState(false);
  const [isUpdating, setIsUpdating] = useState(false);
  const [dropdownPosition, setDropdownPosition] = useState({
    top: 0,
    left: 0,
    width: 0,
  });
  const [mounted, setMounted] = useState(false);
  const buttonRef = useRef<HTMLButtonElement>(null);
  const dropdownRef = useRef<HTMLDivElement>(null);

  // Handle mounting for portal
  useEffect(() => {
    setMounted(true);
    return () => setMounted(false);
  }, []);

  // Handle click outside
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (
        dropdownRef.current &&
        !dropdownRef.current.contains(event.target as Node) &&
        buttonRef.current &&
        !buttonRef.current.contains(event.target as Node)
      ) {
        setIsOpen(false);
      }
    };

    if (isOpen) {
      document.addEventListener("mousedown", handleClickOutside);
    }

    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, [isOpen]);

  // Calculate dropdown position
  useEffect(() => {
    if (isOpen && buttonRef.current) {
      const updatePosition = () => {
        if (buttonRef.current) {
          const rect = buttonRef.current.getBoundingClientRect();
          setDropdownPosition({
            top: rect.bottom + window.scrollY + 4, // 4px gap
            left: rect.left + window.scrollX,
            width: rect.width,
          });
        }
      };

      updatePosition();

      // Update position on scroll
      window.addEventListener("scroll", updatePosition, true);
      window.addEventListener("resize", updatePosition);

      return () => {
        window.removeEventListener("scroll", updatePosition, true);
        window.removeEventListener("resize", updatePosition);
      };
    }
  }, [isOpen]);

  const handleStatusChange = async (newStatus: QuotationStatus) => {
    if (newStatus === currentStatus) {
      setIsOpen(false);
      return;
    }

    setIsUpdating(true);
    try {
      await onStatusUpdate(quoteId, newStatus);
      setIsOpen(false);
    } catch (error) {
      if (axios.isAxiosError(error)) {
        const messages = extractErrorMessages(error);
        setErr(messages);
        toast.error(`${messages}`);
      } else {
        setErr("Something went wrong");
        toast.error("Something went wrong");
      }
    } finally {
      setIsUpdating(false);
    }
  };

  return (
    <>
      <button
        ref={buttonRef}
        onClick={() => setIsOpen(!isOpen)}
        disabled={isUpdating}
        className={`${
          STATUS_CONFIG[currentStatus]?.color || "bg-gray-500"
        } text-white px-4 py-1.5 text-sm rounded-md capitalize flex items-center gap-2 hover:opacity-90 transition-opacity disabled:opacity-50 disabled:cursor-not-allowed min-w-[120px] justify-between`}
      >
        {STATUS_CONFIG[currentStatus]?.label || currentStatus}
        {isUpdating ? (
          <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin" />
        ) : (
          <FaChevronDown
            className={`transition-transform duration-200 ${
              isOpen ? "rotate-180" : ""
            }`}
          />
        )}
      </button>

      {mounted &&
        isOpen &&
        createPortal(
          <div
            ref={dropdownRef}
            style={{
              position: "absolute",
              top: `${dropdownPosition.top}px`,
              left: `${dropdownPosition.left}px`,
              width: `${dropdownPosition.width}px`,
              zIndex: 9999,
            }}
            className="bg-white dark:bg-gray-800 rounded-md shadow-lg border border-gray-200 dark:border-gray-700 max-h-64 overflow-y-auto"
          >
            {AVAILABLE_STATUSES.map((status) => (
              <button
                key={status}
                onClick={() => handleStatusChange(status)}
                disabled={isUpdating}
                className={`w-full px-4 py-2.5 text-left hover:bg-gray-100 dark:hover:bg-gray-700 transition-colors flex items-center justify-between group disabled:opacity-50 disabled:cursor-not-allowed ${
                  status === currentStatus
                    ? "bg-gray-50 dark:bg-gray-700/50"
                    : ""
                }`}
              >
                <span className="flex items-center gap-2">
                  <span
                    className={`w-2 h-2 rounded-full ${STATUS_CONFIG[status].color}`}
                  />
                  {STATUS_CONFIG[status].label}
                </span>
                {status === currentStatus && (
                  <FaCheck className="w-3 h-3 text-green-600" />
                )}
              </button>
            ))}
          </div>,
          document.body
        )}
    </>
  );
};

const page = (): JSX.Element => {
  const [isLoading, setIsLoading] = useState(false);
  const [quoteData, setQuoteData] = useState<Quotation[]>([]);
  const [wonQuoteData, setWonQuoteData] = useState<Quotation[]>([]);
  const [lostQuoteData, setLostQuoteData] = useState<Quotation[]>([]);
  const [err, setErr] = useState("");
  const [currentPage, setCurrentPage] = useState(1);
  const [wonCurrentPage, setWonCurrentPage] = useState(1);
  const [lostCurrentPage, setLostCurrentPage] = useState(1);
  const [pageLimit, setPageLimit] = useState(10);
  const [wonPageLimit, setWonPageLimit] = useState(10);
  const [lostPageLimit, setLostPageLimit] = useState(10);
  const [totalPages, setTotalPages] = useState(1);
  const [currentTab, setCurrentTab] = useState(1);
  const { hasPermission } = usePermissions();

  const getQuotationData = async () => {
    setIsLoading(true);
    try {
      const result = await QuoteService.getAll(currentPage, pageLimit);
      if (result.status === 200) {
        const data = result.data.data;
        setQuoteData(data.data);
        setCurrentPage(data.page);
        setPageLimit(data.limit);
        setTotalPages(data.totalPages);
      }
    } catch (error) {
      process.env.NEXT_PUBLIC_ENV === "development" && console.error(error);
      if (axios.isAxiosError(error)) {
        const messages = extractErrorMessages(error);
        setErr(messages);
      } else {
        setErr(["Something went wrong"]);
      }
    } finally {
      setIsLoading(false);
    }
  };

  const handleTabs = async (no: number): Promise<void> => {
    setCurrentTab(no);
    if (no === 2) {
      await getQuotationData();
    }
    if (no === 3) {
      await getQuotationData();
    }
  };

  const updateQuoteStatus = async (
    id: string,
    newStatus: QuotationStatus
  ): Promise<void> => {
    try {
      const result = await QuoteService.updateStatus(id, {
        quotationStatus: newStatus,
      });

      if (result.status === 200) {
        toast.success(
          `Quotation status updated to ${STATUS_CONFIG[newStatus].label} successfully`
        );

        setQuoteData((prevData) =>
          prevData.map((quote) =>
            quote.id === id
              ? { ...quote, quotationStatus: newStatus }
              : quote
          )
        );
      }
    } catch (error) {
      process.env.NEXT_PUBLIC_ENV === "development" && console.error(error);
      if (axios.isAxiosError(error)) {
        const messages = extractErrorMessages(error);
        setErr(messages);
        toast.error(`Failed to update status: ${messages}`);
      } else {
        setErr(["Something went wrong"]);
        toast.error("Failed to update status");
      }
      throw error;
    }
  };

  useEffect(() => {
    getQuotationData();
  }, []);

  return (
    <div
      className="p-4 sm:ml-64 dark:bg-gray-950 min-h-screen"
      style={{ marginLeft: SIDEBAR_WIDTH }}
    >
      <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center mb-6 gap-4">
        <div>
          <h1 className="text-2xl font-bold text-gray-800 dark:text-white">
            All Sales Quotations
          </h1>
          <p className="text-sm text-gray-600 dark:text-gray-400 mt-1">
            View and edit quotations
          </p>
        </div>

        <div className="flex items-center gap-4">
          <select
            className="border border-gray-300 dark:border-gray-700 rounded-md px-3 py-2 dark:bg-gray-800 dark:text-white"
            value={pageLimit}
            onChange={(e) => setPageLimit(Number(e.target.value))}
          >
            <option value={10}>10</option>
            <option value={20}>20</option>
            <option value={30}>30</option>
            <option value={50}>50</option>
          </select>
        </div>
      </div>

      <div className="mb-6">
        <div className="flex gap-2 border-b border-gray-200 dark:border-gray-700">
          <TabsButton
            title="All Leads"
            isActive={currentTab === 1}
            onClick={() => setCurrentTab(1)}
          />
          <TabsButton
            title="Won"
            isActive={currentTab === 2}
            onClick={() => handleTabs(2)}
          />
          <TabsButton
            title="Lost"
            isActive={currentTab === 3}
            onClick={() => handleTabs(3)}
          />
        </div>
      </div>

      <div className="overflow-x-auto">
        {currentTab === 1 && (
          <table className="w-full bg-white dark:bg-gray-800 rounded-lg shadow">
            <thead className="bg-gray-50 dark:bg-gray-700">
              <tr>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-300 uppercase tracking-wider">
                  Quote Id
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-300 uppercase tracking-wider">
                  Quote Title
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-300 uppercase tracking-wider">
                  Client Name
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-300 uppercase tracking-wider">
                  Quotation Status
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-300 uppercase tracking-wider">
                  Created Date
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-300 uppercase tracking-wider">
                  Actions
                </th>
              </tr>
            </thead>
            <tbody className="divide-y divide-gray-200 dark:divide-gray-700">
              {!isLoading ? (
                quoteData &&
                Array.isArray(quoteData) &&
                quoteData.length > 0 ? (
                  quoteData.map((item, i) => {
                    let rr = i % 2 == 0;
                    const date = formatDate(item.createdAt);
                    return (
                      <tr
                        key={item.id}
                        className={`${
                          rr
                            ? "bg-white dark:bg-gray-800"
                            : "bg-gray-50 dark:bg-gray-700/50"
                        } hover:bg-gray-100 dark:hover:bg-gray-700 transition-colors`}
                      >
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900 dark:text-gray-100">
                          <button
                            onClick={() => handleCopy(item.quoteId)}
                            className="hover:text-blue-600 dark:hover:text-blue-400"
                          >
                            {item.quoteId}
                          </button>
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900 dark:text-gray-100">
                          {item.title}
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900 dark:text-gray-100">
                          {item.customerName}
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap text-sm">
                          <StatusDropdown
                            currentStatus={item.quotationStatus}
                            quoteId={item.id}
                            onStatusUpdate={updateQuoteStatus}
                          />
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900 dark:text-gray-100">
                          {date}
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap text-sm">
                          <div className="flex items-center gap-2">
                            {hasPermission(PERMISSIONS.readLead) ? (
                              <Link href={`/quotation/${item.id}`}>
                                <FaRegEye className="w-5 h-5 text-blue-600 hover:text-blue-800 cursor-pointer" />
                              </Link>
                            ) : (
                              <FaRegEye className="w-5 h-5 text-gray-400 cursor-not-allowed" />
                            )}
                            {hasPermission(PERMISSIONS.updateLead) ? (
                              <Link href={`/quotation/edit/${item.id}`}>
                                <MdOutlineEdit className="w-5 h-5 text-green-600 hover:text-green-800 cursor-pointer" />
                              </Link>
                            ) : (
                              <MdOutlineEdit className="w-5 h-5 text-gray-400 cursor-not-allowed" />
                            )}
                          </div>
                        </td>
                      </tr>
                    );
                  })
                ) : (
                  <tr>
                    <td
                      colSpan={6}
                      className="px-6 py-12 text-center text-gray-500 dark:text-gray-400"
                    >
                      <div className="flex flex-col items-center gap-2">
                        <MdOutlineLeaderboard className="w-12 h-12 text-gray-400" />
                        <p className="text-lg font-medium">Data not found</p>
                      </div>
                    </td>
                  </tr>
                )
              ) : (
                Array.from({ length: 10 }).map((item, i) => {
                  let rr = i % 2 == 0;
                  return (
                    <tr
                      key={i}
                      className={`${
                        rr
                          ? "bg-white dark:bg-gray-800"
                          : "bg-gray-50 dark:bg-gray-700/50"
                      }`}
                    >
                      <td className="px-6 py-4">
                        <Skeleton height={20} />
                      </td>
                      <td className="px-6 py-4">
                        <Skeleton height={20} />
                      </td>
                      <td className="px-6 py-4">
                        <Skeleton height={20} />
                      </td>
                      <td className="px-6 py-4">
                        <Skeleton height={32} width={120} />
                      </td>
                      <td className="px-6 py-4">
                        <Skeleton height={20} />
                      </td>
                      <td className="px-6 py-4">
                        <Skeleton height={20} width={60} />
                      </td>
                    </tr>
                  );
                })
              )}
            </tbody>
          </table>
        )}
      </div>
    </div>
  );
};

export default page;